<template>
  <div class="card-wrapper">
    <div class="card" :class="colorClass" @mouseenter="showHover" @mouseleave="hideHover">
      <img class="card-icon" :src=(cardIcon) v-if="cardIcon">
      <div class="card-title">{{title}}</div>
      <div class="card-hover" v-if="active">
        <img :src=(img) alt="" v-if="img">
        <div class="card-description" v-else v-html="cardText"></div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Card",
    props: ['title', 'img', 'cardType', 'cardIcon', 'cardText'],
    data() {
      return {
        active: false
      }
    },
    methods: {
      showHover() {
        this.active = true
      },
      hideHover() {
        this.active = false
      }
    },
    computed: {
      colorClass() {
        return this.cardType.toLowerCase().replace(/\s/g, '');
      }
    },
  }
</script>

<style scoped lang="scss">
  .card-wrapper {
    width: 20%;
    padding: 0 5px;
  }

  .card {
    width: 100%;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    user-select: none;
    margin-bottom: 10px;
    position: relative;
    color: rgba(#fff, .7);
    display: flex;
    align-items: center;
    &.hero {
      background-color: #7f8c8d;
    }
    &.spell {
      background-color: #c0392b;
    }
    &.passiveability {
      background-color: #1abc9c;
    }
    &.creep {
      background-color: #95a5a6;
    }
    &.ability {
      background-color: #e67e22;
    }
    &.improvement {
      background-color: #8e44ad;
    }
    &.item {
      background-color: #3498db;
    }
  }

  .card-title {
    font-weight: bold;
  }

  .card-icon {
    width: 20px;
    margin-right: 8px;
  }

  .card-hover {
    position: absolute;
    top: 30px;
    left: 0;
    right: 0;
    z-index: 10;
    img {
      width: 100%;
    }
  }

  .card-description {
    background-color: #000000;
    padding: 15px;
  }
</style>
