import vue from 'vue';
import vuex from 'vuex';
import cards from '../cards1';

vue.use(vuex);

export default new vuex.Store({
  state: {
    cards: cards
  },

  getters: {
    getAllCards(state) {
      return state.cards['card_set']['card_list']
    },

    getHeroes(state) {
      let cards = state.cards['card_set']['card_list'];
      return cards.filter((item) => {
        return item['card_type'] === 'Hero'
      })
    },

    getSpells(state) {
      let cards = state.cards['card_set']['card_list'];
      return cards.filter((item) => {
        return item['card_type'] === 'Spell'
      })
    },

    getPassiveAbilities(state) {
      let cards = state.cards['card_set']['card_list'];
      return cards.filter((item) => {
        return item['card_type'] === 'Passive Ability'
      })
    },

    getCreeps(state) {
      let cards = state.cards['card_set']['card_list'];
      return cards.filter((item) => {
        return item['card_type'] === 'Creep'
      })
    },

    getAbilities(state) {
      let cards = state.cards['card_set']['card_list'];
      return cards.filter((item) => {
        return item['card_type'] === 'Ability'
      })
    },

    getImprovements(state) {
      let cards = state.cards['card_set']['card_list'];
      return cards.filter((item) => {
        return item['card_type'] === 'Improvement'
      })
    },

    getItems(state) {
      let cards = state.cards['card_set']['card_list'];
      return cards.filter((item) => {
        return item['card_type'] === 'Item'
      })
    },
  }
});
